/* Container for the radio group */
.radio-group {
  display: flex;
  gap: 20px;
  align-items: center;
  justify-content: center;
  font-family: Arial, sans-serif;
}

/* Label for each radio option */
.radio-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 17px;
  color: rgb(243, 236, 236);
}

/* Hide the native radio input */
.radio-input {
  display: none;
}

/* Custom radio button */
.radio-button {
  width: 20px;
  height: 20px;
  border: 2px solid #1976d2;
  border-radius: 50%;
  margin-right: 10px;
  display: inline-block;
  background-color: white;
  position: relative;
  transition: all 0.3s ease-in-out;
}

/* Checked state */
.radio-input:checked + .radio-button {
  background-color: #1976d2;
  border-color: #1976d2;
}

/* Inner circle for the checked state */
.radio-input:checked + .radio-button::after {
  content: "";
  width: 10px;
  height: 10px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Hover effect */
.radio-label:hover .radio-button {
  border-color: #005cbf;
}

div {
  border-width: 3px;
  border-style: solid;
  border-color: #282c34;

  border-top: 3px;
  border-right: 5px;
  border-bottom: 2px;
  border-left: 4px;
}
