a {
    display: inline-block;
    height: 100px;
    width: 100px;
    margin: 50px;
    border-radius: 140px;
    border: 3px solid #27ae60;
    background: #cc2e36;
    text-align: center;
    text-decoration: none;
    color: #f4f6f5;
    transition: all 0.2s;
  }
  
  i {
    line-height: 100px;
    font-size: 22px;
    font-weight: bold;
    font-style: normal;
    transition: all 0.1s;
  }
  
  .effect1 {
    box-shadow: 0 0 0 0 #FFF, 0 0 0 0 #27ae60, 0 0 0 0 #FFF, 0 0 0 0 #27ae60;
    margin: 25px;
  }
  .effect1:hover {
    border-color: #FFF;
    box-shadow: 0 0 0 7px #FFF, 0 0 0 10px #27ae60, 0 0 0 17px #FFF, 0 0 0 20px #27ae60;
  }
  .effect1:active {
    box-shadow: 0 0 0 15px #FFF, 0 0 0 18px #27ae60, 0 0 0 17px #FFF, 0 0 0 20px #27ae60;
  }
  
  .effect1_alt {
    box-shadow: 0 0 0 0 #FFF, 0 0 0 0 #27ae60, 0 0 0 0 #FFF, 0 0 0 0 #27ae60;
  }
  .effect1_alt:hover {
    box-shadow: 0 0 0 10px #FFF, 0 0 0 13px #27ae60, 0 0 0 20px #FFF, 0 0 0 23px #27ae60;
    animation: effect1_alt 1s linear 0s infinite;
  }
  .effect1_alt:active {
    box-shadow: 0 0 0 18px #FFF, 0 0 0 21px #27ae60, 0 0 0 20px #FFF, 0 0 0 23px #27ae60;
  }
  
  .effect2:hover {
    box-shadow: 0px 0 0 0 #1f1f1f, 0px 0 0 10px #27ae60;
  }
  .effect2:active {
    box-shadow: 0px 0 0 11px #FFF, 0px 0 0 10px #27ae60;
  }
  
  .effect3:hover {
    box-shadow: 0px 0 0 0 #FFF, 0px 0 0 10px #27ae60;
  }
  .effect3:active {
    box-shadow: 0px 0 0 11px #FFF, 0px 0 0 10px #27ae60;
    background-color: #FFF;
  }
  .effect3:active i {
    color: #27ae60;
  }
  
  .effect4:hover {
    box-shadow: 0px 0 0 50px #99f5ab inset;
  }
  .effect4:active {
    background-color: #FFF;
    box-shadow: 0px 0 0 50px #FFF inset;
    color: #27ae60;
  }
  
  .effect5:hover {
    box-shadow: 0px 0 0 11px #FFF, 0px 0 0 10px #27ae60, 0px 0 0 50px #FFF inset;
  }
  .effect5:active {
    box-shadow: 0px 0 0 11px #27ae60, 0px 0 0 10px #27ae60, 0px 0 0 50px #FFF inset;
  }
  .effect5:active i {
    color: #27ae60;
  }
  
  .effect6 {
    background-color: #FFF;
    box-shadow: 0px -100px 0 0 #2ecc71 inset;
  }
  .effect6:hover {
    box-shadow: 0px 0 0 0 #2ecc71 inset;
  }
  .effect6:active {
    box-shadow: 0px 100px 0 0 #2ecc71 inset;
  }
  
  .effect7:hover {
    border-bottom-right-radius: 0;
  }
  
  .effect8 {
    transform: rotateZ(-45deg);
  }
  .effect8:hover {
    border-top-right-radius: 0;
  }
  .effect8:active {
    box-shadow: 0px -13px 0 #36BD6F inset;
  }
  .effect8 i {
    transform: rotateZ(45deg);
    display: inline-block;
  }
  
  .effect9 {
    transform: rotateZ(-45deg);
  }
  .effect9:hover {
    border-top-right-radius: 0;
  }
  .effect9 :active {
    box-shadow: -90px 90px 0 #36BD6F inset;
  }
  .effect9 i {
    transform: rotateZ(45deg);
    display: inline-block;
  }
  
  @keyframes effect1_alt {
    from {
      box-shadow: 0 0 0 10px #FFF, 0 0 0 13px #27ae60, 0 0 0 20px #FFF, 0 0 0 23px #27ae60;
    }
    40% {
      box-shadow: 0 0 0 13px #FFF, 0 0 0 16px #27ae60, 0 0 0 26px #FFF, 0 0 0 29px #27ae60;
    }
    90% {
      box-shadow: 0 0 0 3px #FFF, 0 0 0 6px #27ae60, 0 0 0 14px #FFF, 0 0 0 17px #27ae60;
    }
    to {
      box-shadow: 0 0 0 10px #FFF, 0 0 0 13px #27ae60, 0 0 0 20px #FFF, 0 0 0 23px #27ae60;
    }
  }