.App {
  background-color: #282c34;
  min-height: 95vh;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(247, 244, 244);
  vertical-align: middle;
 
  border-radius: 75px;
  width: 400px;
  padding: 10px;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

